.lp-link {
	font-size      : rem(14px);
	line-height    : rem(24px);
	position       : relative;
	text-decoration: none;

	&::before {
		content : "";
		position: absolute;
		left    : 0;
		height  : rem(1px);
		width   : rem(36px);
		bottom  : -0.5rem;
	}

	@include media-breakpoint-up(lg) {
		font-size  : rem(16px);
		line-height: rem(26px);
	}

	&--pri {
		color: $second;

		&::before {
			background: $second;
		}
	}

	&--sm {
		color      : $primary;
		font-size  : rem(12px);
		line-height: rem(22px);
		@extend .fnt-800;
		text-transform: uppercase;

		&::before {
			content   : "";
			position  : absolute;
			left      : 0;
			height    : rem(2px);
			width     : rem(21px);
			bottom    : -0.5rem;
			background: $primary;
		}
	}

	&--red {
		&:hover {
			color:#FD2E35;
		}
	}
}

a {
	text-decoration: none;

}