.lp-sudo-right {
	position: relative;

	&::before {
		content   : "";
		position  : absolute;
		background: $second;
		left      : rem(-18px);
		top       : rem(7px);
		height    : rem(30px);
		width     : rem(3px);

		@include media-breakpoint-up(md) {
			top: rem(14px);
		}

		@include media-breakpoint-up(lg) {
			left  : rem(-40px);
			top   : rem(12px);
			height: rem(73px);
			width : rem(5px);
		}
	}
}