.moment-list {
	&__item {
		margin-bottom: 30px;
		
		@media (min-width: 768px) {
			margin-bottom: 33px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.val-li {
	&__item {
		margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 70px;
		}

		@media (min-width:992px) {
			margin-bottom: 100px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.client-list {
	&__item {
		// margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 65px;
		}

		@media (min-width: 992px) {
			margin-bottom: 94px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.clnt {
	position: relative;
	transition: 0.3s ease-in-out;
	overflow: hidden;
	cursor: pointer;
	
	@media (min-width: 768px) {
		padding-left: 25px;

	}

	@media (min-width: 992px) {
		padding-left: 35px;
	}

	&::before {
		@media (min-width: 768px) {
			position: absolute;
			content: "";
			top: 3px;
			left: 0;
			width: 4px;
			height: calc(100% - 5px);
			background: #221429;
			transition: 0.4s ease-in-out;
		}
		
	}

	&:hover {
		.grayscale {
			filter: none;
			-webkit-filter: grayscale(0%);
		}
	}

	&__logo {
		width: 90px;
		height: 30px;
		margin-bottom: 20px;

		@media (min-width: 768px) {
			width: 100px;
			height: 35px;
		}

		@media (min-width: 992px) {
			width: 156px;
			height: 46px;
			margin-bottom: 25px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__profile {
		.img {
			width: 54px;
			height: 54px;
			border-radius: 50%;
			background-color: #C4C4C4;
			display: flex;
			justify-content: center;
			align-items: center;

			img {
				width: 100%;
				height: 100%;
				border-radius: 50%;
				object-fit: cover;
			object-position: center;
			}
		}
	}
}

.team {
	margin-bottom: 40px;

	@media (min-width: 768px) {
		margin-bottom: 70px;
	}

	@media (min-width: 992px) {
		margin-bottom: 100px;
	}

	&:last-of-type {
		margin-bottom: 0;
	}
	
	&__head {
		margin-bottom: 25px;

		@media (min-width: 768px) {
			margin-bottom: 30px;
		}
	}

	.team-profile {
		&__img {
			margin-bottom: 15px;
			overflow: hidden;

			img {
				width: 100%;
			}
		}
	}
}

.team-list {
	&__item {
		margin-top: 30px;
		overflow: hidden;

		@media (min-width: 768px) {
			margin-top: 40px;
		}

		@media (min-width: 992px) {
			margin-top: 60px;
		}

		&:nth-child(1) {
			margin-top: 0;
		}

		&:nth-child(2) {
			margin-top: 0;
		}

		&:nth-child(3) {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}
	}
}


// Team
.team-profile-img {
	img {
		width: 100%;
		min-height: auto;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}
}

.profile {
	@media (min-width: 768px) {
		height: 100%;
		padding: 0 0 10px 10px;
	}
}

.team-view-btn {
	&__item {
		&:nth-child(even) {
			text-align: right;
			display: flex;
			align-items: center;
			justify-content: end;
			.team-nxt-view {
				margin-left: auto;
				&__profile {
					order: 2;
					margin-left: auto;
					
				}

				&__name {
					order: 1;
					padding-left: 0;

					@media (min-width: 768px) {
						padding-right: 16px;
					}
					
				}
			}
		}

		
	}
}

.team-nxt-view {
	&__profile {
		width: 78px;
		height: 78px;
		min-width: 78px;

		@media (max-width: 767px) {
			margin-bottom: 10px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__name {
		@media (min-width: 768px) {
			padding-left: 16px;
			height: 100%;
		}

		h4 {
			transition: 0.3s ease-in-out;
		}
		
	}

	&:hover {
		.team-nxt-view__name h4 {
			color: #fd2e35;
		}
	}
}

