body {
	position: relative;


	@media (min-width: 768px) {
		padding: 20px;
	}

	* {
		letter-spacing: normal;
	}

	p,li {
		font-size: 16px;
		line-height: 26px;

		@media (min-width: 768px) {
			font-size: 16px;
			line-height: 26px;
		}
	}
}




.fnt-loui {
	font-family: "Louisville Script W07 Bold" !important;
}

.bd-brd {
	position: fixed;
	content: "";
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	border: 15px solid #ffffff;
	pointer-events: none;
	z-index: 1031;

	@media (max-width: 767px) {
		display: none !important;
		opacity: 0;
	}

	@media (min-width: 768px) {
		border: 20px solid #ffffff;
	}

	

	&--gray {
		border-color: #F7F4F2;
	}
}



.h1 {
	letter-spacing: -3px;
	font-weight: $fnt-800;

	@include media-breakpoint-up(md) {
		font-size: rem(40px);
		line-height: rem(55px);
	}

	@include media-breakpoint-up(lg) {
		font-size: rem(60px);
		line-height: rem(75px);
	}

	&--lg {
		font-size: rem(40px);
		line-height: rem(50px);

		@include media-breakpoint-up(md) {
			font-size: rem(60px);
			line-height: rem(75px);
		}

		@include media-breakpoint-up(lg) {
			font-size: rem(100px);
			line-height: rem(110px);
		}
	}
}

.h2 {
	letter-spacing: -3px;
	font-weight: $fnt-800;

	@include media-breakpoint-up(md) {
		font-size: rem(34px);
		line-height: rem(46px);
	}

	@include media-breakpoint-up(lg) {
		font-size: rem(44px);
		line-height: rem(56px);
	}


	&--lg {
		// letter-spacing: -0.03em;
		font-weight: $fnt-800;
		font-size: rem(30px);
		line-height: rem(40px);

		@include media-breakpoint-up(md) {
			font-size: rem(40px);
			line-height: rem(55px);
		}

		@include media-breakpoint-up(lg) {
			font-size: rem(50px);
			line-height: rem(65px);
		}
	}
}

.h3 {

	font-weight: $fnt-800;
	letter-spacing: -1px;

	@include media-breakpoint-up(md) {
		font-size: rem(28px);
		line-height: rem(38px);
		letter-spacing: -2px;
	}

	@include media-breakpoint-up(lg) {
		font-size: rem(30px);
		line-height: rem(40px);
	}
}



// font-weights

.fnt-400 {
	font-weight: 400;
}

.fnt-600 {
	font-weight: 600;
}

.fnt-500 {
	font-weight: 500;
}

.fnt-700 {
	font-weight: 700;
}

.fnt-800 {
	font-weight: 800;
	
}

.l-2 {
	letter-spacing: -2px !important;
}

.bg-gray {
	background: #f7f4f2;
}

.main {
	transition: 0.3s ease-in-out;
}