.image-text-list {
	&__item {
		background: #ffffff;
		padding: 15px 15px 36px 15px;

		@media (min-width: 768px) {
			padding: 30px;
		}

		@media (min-width: 992px) {
			padding: 40px;
		}

		.img {
			@media (min-width: 768px) {
				width: 100%;
				height: 100%;
			}

			@media (max-width: 767px) {
				margin-bottom: 25px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center;

				@media (max-width: 767px) {
					height: auto;
				}
			}
		}

		.text {
			@media (min-width: 768px) {
				padding-top: 15px;
				margin-top: auto;
				padding-left: 40px;
				margin-bottom: 15px;
			}

			@media (min-width: 992px) {
				padding-left: 60px;
			}

			width: 100%;
			height: 100%;
		}

		margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 60px;
		}

		@media (min-width: 992px) {
			margin-bottom: 80px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.ly-6 {
	margin: 6px 0;
}

.industry-list {
	&__item {
		margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 60px;
		}

		@media (min-width: 992px) {
			margin-bottom: 80px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		h3 {
			@media (min-width: 768px) {
				text-align: right;
			}
		}
	}
}
