.bg-shadow {
	position: relative;

	&:after {
		content       : "";
		position      : absolute;
		width         : 100%;
		height        : -webkit-fill-available;
		z-index       : 1;
		pointer-events: none;
	}

	&--top {
		&:after {
			right     : 0;
			top       : 0;
			background: linear-gradient(218.71deg, #FD2E35 -3.1%, rgba(129, 23, 27, 0.016) 46.78%, rgba(127, 23, 27, 0) 67.76%);
		}
	}

	&--bottom {
		&:after {
			left      : 0;
			bottom    : 0;
			background: linear-gradient(41.42deg, #FD2E35 -3.12%, rgba(255, 255, 255, 0.016) 57.03%, rgba(127, 23, 27, 0) 100.23%);
		}
	}
}

