
 .contact-bnr {
    margin-bottom: 30px;

    @media (min-width: 768px) {
       
        margin-bottom: 40px;
    }

    @media (min-width: 992px) {
      
        margin-bottom: 70px;
    }
}

.contact {
    padding: 20px 15px;
   
    @media (min-width: 768px) {
        padding: 30px 25px;
       
    }

    @media (min-width: 992px) {
        padding: 40px 35px;
    }
}

.contact-head {
    margin-bottom: 20px;

    @media (min-width: 768px) {
       
        margin-bottom: 40px;
    }

    @media (min-width: 992px) {
      
        margin-bottom: 60px;
    }
}

.contat-list {
    &--bnr {
        .contat-list__item {
            // @media (max-width: 767px) {
            //     margin-bottom: 0;
            // }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__item {
        @media (max-width: 767px) {
            margin-bottom: 24px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        @media (min-width: 768px) {
            margin-top: 25px;
        }

        &:nth-child(1),&:nth-child(2),&:nth-child(3) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }
    }
}

.contct-place {
    .contat-list {
        &__item {
            margin-top: 40px;

            @media (min-width: 992px) {
                margin-top: 40px;
            }

            &:nth-child(1) {
                margin-top: 0;
            }

            &:nth-child(2) {

                @media (min-width: 768px) {
                    margin-top: 0;
                }
            }
        }
    }

    &__item {
        margin-bottom: 60px;

        @media (min-width: 768px) {
        
            margin-bottom: 70px;
        }

        @media (min-width: 992px) {
        
            margin-bottom: 100px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    .place-name  {
       
        @media (max-width: 767px) {
            margin-bottom: 10px;
        }
    }
}

.mb-contact-head {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 20px;
    background-color: #ffffff;
    z-index: 1032;
    transform: translateY(-100%);
    transition: 0.6s ease-in-out;

    span {
        color: #fd2e35;
        font-size: 20px;
        letter-spacing: -0.5px;
        font-weight: 800;
        display: block;
    }

    &.active {
        transform: translateY(0);
    }
}

.card-hvr {
    transition: 0.3s ease-in-out;
    &:hover {
        box-shadow: 1px 8px 20px grey;
       
    }
}

// Enquiry
.enquiry-head  {
    margin-bottom: 40px;

    @media (min-width: 768px) {
        margin-bottom: 50px;
    }

    @media (min-width: 992px) {
        margin-bottom: 70px;
    }
}

.input {
    outline: 0;
    appearance: none;
    box-shadow: none;
    background-color: #ffffff;
    border: 1px solid rgba(34, 20, 41, 0.14);
    resize: none;
    width: 100%;
    padding: 8px 16px;
    position: relative;

    @media (min-width: 768px) {
        padding: 10px 20px;
        min-height: 40px;
    }

    @media (min-width: 992px) {
        padding: 13px 26px;
        min-height: 52px;
    }

    input {
        outline: none;
        border: 0;
        background-color: none;
        appearance: none;
        width: 100%;

        &::placeholder {
            color: rgba(34, 20, 41, 0.6);
            font-weight: 14px;
    
            @media (min-width: 992px) {
                font-size: 16px;
            }
        }
    }

    &__require {
        display: inline-block;
        color: #FD2E35;
        text-transform: capitalize;
        position: absolute;
        right: 16px;
        top: 50%;
        transform: translateY(-50%);

        @media (min-width: 768px) {
            right: 20px;
        }

        @media (min-width: 992px) {
            right: 26px;
        }
    }

    textarea {
        outline: none;
        border: 0;
        background-color: none;
        appearance: none;
        width: 100%;
        min-height: 100px;
        resize: none;
    
        @media (min-width: 768px) {
            min-height: 150px;
        }
    
        @media (min-width: 992px) {
            min-height: 192px;
        }
        
        &::placeholder {
            font-weight: 14px;
            color: rgba(34, 20, 41, 0.6);
            @media (min-width: 992px) {
                width: 60%;
                font-size: 16px;
            }
        }
    }

    &--textarea {
        .input__require {
            top: auto;
            bottom: 8px;

            @media (min-width: 768px) {
                bottom: 10px;
            }
    
            @media (min-width: 992px) {
                bottom: 13px;
            }

        }
    }
}

.label {
    color: rgba(34, 20, 41, 0.6);
}

.enquiry-form {
    margin-bottom: 40px;

    @media (min-width: 768px) {
        margin-bottom: 40px;
    }

    @media (min-width: 992px) {
        margin-bottom: 45px;
    }

    &__item {
        margin-bottom: 30px;

        @media (min-width: 768px) {
            margin-bottom: 40px;
        }
    
        @media (min-width: 992px) {
            margin-bottom: 50px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    .parsley-errors-list {
        li {
            font-weight: 800 !important;
            font-size: 14px !important;
        }
    }
}

.mb-fld {
    margin-bottom: 30px;

    @media (min-width: 992px) {
        margin-bottom: 35px;
    }
}

.file {
    &__attach {
        position: relative;
        .file-uplod{
            border: 1px solid #221429;
            padding: 8px 26px;
            cursor: pointer;
    
            @media (min-width: 768px) {
                padding: 10px 16px;
            }
    
            @media (min-width: 992px) {
                padding: 16px 20px;
            }
        }
    }
}

#file-upload {
    display: none;
}

.file-reset {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    background-color: #221429;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    transform: translate(50%, -50%);

    span {
        color: #ffffff;
    }

    &:hover {
        color: #ffffff;
        span {
            font-weight: 800;
        }
    }
}

.mb-field-80 {
    margin-bottom: 50px !important;

    @media (min-width: 768px) {
        margin-bottom: 60px !important;
    }

    @media (min-width: 992px) {
        margin-bottom: 80px !important;
    }
}

.js-submit {
    &.valid {
        background: #FD2E35 !important;
        color: #ffffff !important;
    }
}


.contact-title {
    @media (max-width: 767px) {
        font-size: 24px !important;
        line-height: 34px !important;
    }
}