.hvr-r-l {
    position: relative;
    transition: 0.2s ease-in-out;

    @media (max-width: 767px) {
        color: #FD2E35;
    }

    &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 0;
        height: 1px;
        background-color: #FD2E35;
        transition: 0.3s ease-in-out;

        @media (max-width: 767px) {
            width: 10%;
        }
    }
    &:hover {
        color: #FD2E35;

        &::after {
            width: 30%;
        }
    }
}

.design-audit-step {
    &__item {
        margin-bottom: 30px;

        @media (min-width: 768px) {
            margin-bottom: 40px;
        }

        @media (min-width: 992px) {
            margin-bottom: 60px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.audit-list {
    &__item {
        .audit {
            padding: 20px;
            background-color: #ffffff;

            @media (min-width: 768px) {
                padding: 22px 30px 30px 30px;
                margin-top: 25px;
            }

            @media (min-width: 992px) {
                padding: 28px 40px 40px 40px;
                margin-top: 30px;
            }

            &:nth-child(1),&:nth-child(2),&:nth-child(3) {
                @media (min-width: 768px) {
                    margin-top: 0;
                }
            }
        }
    }
}