.page-head {
    margin-bottom: 40px;

    @media (min-width: 768px) {
        margin-bottom: 60px;
    }

    @media (min-width: 992px) {
        margin-bottom: 70px;
    }

    &--mb0 {
        @media (max-width: 767px) {
            margin-bottom: 0;
        }
    }
}

.case-img-list {
    &__item {
        .case-std {
            height: 280px;
            position: relative;
            display: flex;
            align-items: flex-end;
            padding: 15px;
            overflow: hidden;
            transition: 0.4s ease-in-out;

            @media (min-width: 768px) {
                height: 400px;
            }

            @media (min-width: 992px) {
                height: 446px;
                padding: 17px;
            }

            .img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;

                img {
                    transition: 0.4s ease-in-out;
                }
            }

            &:hover {
                transform: scale(0.95) !important;

                .case-std__info {
                    box-shadow: 0 4px 16px 0 #fd2e3530;
                }

                .img {
                   
                    img {
                        transform: scale(1.1) !important;
                    }
                }
            }

            &__info {
                background: #ffffff;
                padding: 20px 15px;
                position: relative;
                z-index: 10;
                width: 90%;
                transition: 0.6s ease-in-out;

                @media (min-width: 768px) {
                    padding: 30px 25px;
                   
                }

                @media (min-width: 992px) {
                    padding: 35px 30px;
                    width: 80%;
                }

                * {
                    position: relative;
                    transition: 0.8s ease-in-out;
                }
            }
        }
    }
}

.values-list {
    &__item {
        margin-top: 24px;

        @media (min-width: 768px) {
            margin-top: 45px;
        }

        @media (min-width: 992px) {
            margin-top: 55px;
        }

        &:nth-child(1) {
            margin-top: 0;
        }

        &:nth-child(2) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }
    }

    h6 {
        font-size: 20px;
        line-height: 30px;
        letter-spacing: -0.5px;

        @media (min-width: 992px) {
            font-size: 22px;
            line-height: 32px;
        }
    }

    p {
        font-size: 16px;
        line-height: 26px;

    }
}

