.tab-head-item {
    padding: 0 0 25px 0;
    overflow-x: auto;

    @media (min-width: 768px) {
        padding: 0 0 30px 0;
    }

    @media (min-width: 992px) {
        padding: 0 0 45px 0;
    }

    &__name {
        color: #221429;
        opacity: 0.2;
        position: relative;
        margin-right: 10px;
        cursor: pointer;

        @media (max-width: 767px) {
            font-size: 23px !important;
        }

        @media (min-width: 768px) {
            margin-right: 20px;
        }

        @media (min-width: 1200px) {
            margin-right: 50px;
        }

        &:last-child {
            margin-right: 0;
        }

        &.active {
            opacity: 1;

            &::before {
                opacity: 1;
            }
        }

        &::before {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 25%;
            height: 4px;
            background-color: #221429;
            opacity: 0;
            transition: 0.3s ease-in-out;
        }

        &:hover {
            &::before {
                width: 100%;
            }
        }
    }
}

.logo-list {
    &__item {
        margin-top: 40px;
        display: flex;
        align-items: center;


        @media (min-width: 768px) {
            margin-top: 50px;
        }

        @media (min-width: 992px) {
            margin-top: 70px;
        }

        img {
            @media (min-width: 992px) {
                max-width: 70% !important;
            }
        }

        &:nth-child(1) {
            margin-top: 0;
        }

        &:nth-child(2) {
            margin-top: 0;
        }

        &:nth-child(3) {
            margin-top: 0;
        }

        &:nth-child(4) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }

      
    }

    &--3 {
        .logo-list__item {
            &:nth-child(4) {
                @media (min-width: 768px) {
                    margin-top: 50px;
                }
        
                @media (min-width: 992px) {
                    margin-top: 70px;
                }
            }
        }
    }


    &--mt-15 {
        .logo-list__item {
            margin-top: 15px;

            @media (min-width: 768px) {
                margin-top: 15px;
            }
    
            @media (min-width: 992px) {
                margin-top: 15px;
            }

            &:nth-child(1) {
                margin-top: 0;
            }
    
            &:nth-child(2) {
                margin-top: 0;
            }
    
            &:nth-child(3) {
                margin-top: 0;
            }
    
            &:nth-child(4) {
                @media (min-width: 768px) {
                    margin-top: 0;
                }
            }
        }
    }
}

.expert-member {
    background-color: #221429;
    padding: 25px;

    @media (max-width: 767px) {
        width: calc(100% + 40px);
        margin-left: -20px;
    }

    @media (min-width: 768px) {
        padding: 40px 40px 40px 40px;
    }

    @media (min-width: 992px) {
        padding: 60px;
    }

    &__info {
        @media (max-width: 767px) {
            text-align: center;
        }
    }
}
