header {
	position: fixed;
	top: 30px;
	width: 100%;
	z-index: 1031;

	@media (min-width: 768px) {
		top: 60px;
		margin: 0 -20px;
	}

	@media (min-width: 992px) {
		top: 60px;
	}
}

.lp-mainheader {
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1033;
	.navbar {
	background: transparent;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	transition: 0.3s ease-in-out;
	

	@media (min-width: 768px) {
		top: 30px;
	}

	@media (min-width: 992px) {
		top: 40px;
	}

	@media (min-width: 1200px) {
		top: 45px;
	}

	
	}

	.nav-link {
		color      : $primary;
		font-size  : rem(14px);
		line-height: rem(24px);

		margin: {
			left : rem(8px);
			right: rem(8px);
		}
	}

	.nav-item:last-child {
		.nav-link {
			color: $second;
		}
	}

	&--white {
		.nav-link {
			color:  #FFFFFF;
		}

		.icon-lollypop {
			&::before {
				color: #ffffff;
			}
		}

		.drop-clr {
			display: none;
		}

		.drop-white {
			display: block !important;
		}

		.lp-hamberg__line {
			background: #ffffff;
		}

		.nav-item:last-child {
			.nav-link {
				color:  #FFFFFF;
			}
		}

		.default {
			color:  #FFFFFF !important;
		}

		.menu-title {
			color:  #FFFFFF;
		}

		.header-nav {
			li {

				a {

					&::after {
						background-color: #ffffff;
					}
					&:hover {
						color: #221429 !important;
						&::after, &::before {
							color: #221429;
							background-color: #221429;
						}
					}

					
					&.hvr-line {
						color: #ffffff !important;
		
						&::after {
							background-color: none !important;
						}
					}
				}
			}
		}

	}

	.default {
		color: $second;
	}

	.drop-white {
		display: none;
	}
}

.drop-ar {
	width: 8px;

	@media (min-width: 768px) {
		width: 10px;
	}
}

.navbar-overide {
	padding: {
		top   : rem(15px);
		bottom: rem(15px);
	}

	
}

// Hamberger
.lp-hamberg {
	margin-left: rem(15px);
	
	z-index: 1031;
	transition: 0.3s ease-in-out;
	position: absolute;
	cursor: pointer;
	right: 19px !important;
	top: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	transform: translate(0, -50%);

	@media (max-width: 767px) {
		transform: translate(0, -50%) !important;
		width: 30px;
		height: 30px;
		right: 7px !important;

	}

	&__item {
		width: 60px;
		height: 60px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		@media (min-width: 768px) {
			width: 100px;
			height: 100px;
		}
	}


	* {
		position: relative;
		transition: 0.8s ease-in-out;
		z-index: 999;
	}



	@media (min-width: 768px) {
		right: 0;
		width: 50px;
		height: 50px;
		transform: translate(25px, -50%);
	
	}
	

	&__line {
		display      : block;
		height       : 2px !important;
		width        : 13px !important;
		border-radius: rem(3px);
		background   : $second;
		margin       : rem(4px);
		position: relative;
		transition: 0.3s ease-in-out;
	}
}

// DROPDOWN
.ui.dropdown.lp-dropdown-wrap {
	.menu {
		&.lp-dropdownmenu {
			box-shadow   : 1px 3px 15px #e8e8e8;
			border       : none;
			border-radius: 6px;
			padding      : 0;
		}

		.item {
			&:hover {
				background: pink;
				transition: 0.5s background ease-in;
			}

			span {
				padding: 6px;
			}
		}
	}

	&.active.visible {
		svg {
			transform : rotate(-180deg);
			transition: 0.5s transform ease-in;
		}
	}
}

.header-nav {
	@media (min-width: 768px) {
		padding-right: 30px;
	}


	@media (min-width: 992px) {
		padding-right: 40px;
	}


	@media (min-width: 1200px) {
		padding-right: 55px;
	}

	li {
		@media (min-width: 768px) {
			padding: 0 8px;
		}

		@media (min-width: 992px) {
			padding: 0 10px;
		}


		@media (min-width: 1200px) {
			padding: 0 20px;
		}

	

		&:last-child {
			padding-right: 0;
		}

		position: relative;
			&:hover {
				a {
				
					color: #fd2e35;
					transition: all .6s cubic-bezier(0,0,.23,1);

					&::after {
						width: 100%;
						background-color: #fd2e35;
					}
				}
			}
		
		a {
			position: relative;
			padding: 0 !important;
			transition: 0.6s ease-in-out;
			color: #221429;

			&::after {
				position: absolute;
				content: "";
				bottom: -5px;
				left: 0;
				width: 32px;
				height: 1px;
				background-color: #221429;
				display: none;
				transition: all .6s cubic-bezier(0,0,.23,1);
			}

			&.hvr-line {
				color: #fd2e35 !important;

				&::after {
					background-color: #fd2e35 !important;
				}
			}
		}
	}

	.nav-item {
		a {
			&.active {
				&::after {
					display: block;
				}
			}

			&.hvr-line {
				&::after {
					display: block !important;
				}
			}
		}
		
	}
}

