		display: flex;
		flex-direction: column;
		background: #221429 !important;
	}



	&.active {
		opacity: 1;
    	visibility: visible;
		height: 100vh;
		overflow-y: auto;
		overflow-x: hidden;
		top: 0;
		pointer-events: all;
		transform: translate(0,0);
	}



	&__item {
		width: 100%;
		height: 100%;
		// padding: 15px 0 0 0;
	}

