			padding-bottom: 30px;
		}

		
		

		@media(min-width: 768px) and (max-width: 991px) {
			padding: 100px 50px 50px 50px !important;
		}
	

	
	@media (min-width: 992px) {
		width: 55%;
		height: 100%;
		padding-top: 0;
		padding-bottom: 0;
		// padding-left: 35px;
	}

	@media (min-width: 1200px) {
		// padding-left: 60px;
		width: 60%;
		
	}

	// @media (min-width: 1400px) {
		
	// 	padding-top: 100px;
	// 	padding-bottom: 100px;
	// }
	}

	&__blackbg {
		width: 100%;
		
		background: #221429;
		padding-top: 24px;
		padding-left: 0;
		padding-right: 0;
		float: left;


		@media (max-width: 767px) {
			padding-top: 30px;
			padding-bottom: 30px;
			
		}

		
		
		
		
		@media(min-width: 768px) and (max-width: 991px) {
			padding: 50px 50px 75px 50px!important;
		}


	@media (min-width: 992px) {
