.menu-link {
	position: fixed;
	width: 100%;
	height: 100%;
	right: -100%;
	top: 0;
	z-index: 1030;

	&.active {
		right: 0;
	}

	&__item {
		animation: fadeIn 0.5s linear;
		animation-fill-mode: both;
	}
}

.js-toggle.op-0 {
	opacity: 0;
}

header.sticky {
	z-index: 5;

	@media (min-width: 992px) {
		top: 75px;
	}

	.js-toggle {
		position: relative;
		z-index: -999;
		height: 0;
		
		* {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			cursor: none !important;
		}

		.lp-hamberg {
			opacity: 1 !important;
			visibility: visible !important;
			z-index: 1035;
		}
	}

	.lp-hamberg {
		&__line {
			@media (min-width: 768px) {
				width: 15px !important;
			}
		}

		&::before {
			opacity: 1;
			transform: scale(1);
			box-shadow: 1px 8px 20px grey;
		}
	}
}

.js-hide {
	* {
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
	}

	.lp-hamberg {
		opacity: 1 !important;
		visibility: visible !important;
		z-index: 1024;
		pointer-events: all;
	}
}

.menu-open {
	z-index: 1031 !important;
	
	.navbar {
		box-shadow: none !important;
		z-index: 1031;
	}

	.lp-hamberg {
		&__line {
				background: #ffffff !important;
				width: 15px !important;
		}

		&::before {
			opacity: 0 !important;
		}
	}
}

//Loader
.loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1040;
	background: #F9F6F5;
	transition: 0.6s ease-in-out;

	&.active {
		transform: translateY(-100%);
	}

	&__item {
		position: relative;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #ffffff;


		img {
			width: 50px;
			height: 50px;
			object-fit: contain;

			@media (min-width: 992px) {
				width: 100px;
				height: 100px;
			}
		}
	}

	.load-6 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: flex;
	}

	.load-6 .letter {
		animation-name: loadingF;
		animation-duration: 1.6s;
		animation-iteration-count: infinite;
		animation-direction: linear;
		color: #FD2E35;
		font-size: 25px;
		font-weight: 800;
	}

	.l-1 {
		animation-delay: 0.48s;
	}

	.l-2 {
		animation-delay: 0.6s;
	}

	.l-3 {
		animation-delay: 0.72s;
	}

	.l-4 {
		animation-delay: 0.84s;
	}

	.l-5 {
		animation-delay: 0.96s;
	}

	.l-6 {
		animation-delay: 1.08s;
	}

	.l-7 {
		animation-delay: 1.2s;
	}

	.l-8 {
		animation-delay: 1.32s;
	}
}

@keyframes loadingF {
	0% {
	  opacity: 0;
	}

	100% {
	  opacity: 1;
	}
}

