.hvr-div {
	a {
		
		position: relative;
		transition: 0.3s ease-in-out;
		display: inline-block !important;

		&::before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 0;
			height: 1px;
			background-color: #ffffff;
			transition: 0.3s ease-in-out;

			@media (max-width: 767px) {
				display: none;
			}
		}

		&:hover {
		
	
			&:before {
				width: 100%;
			}
	
			
		}
	}
	
	
}

