#progress {
	height: 40px;
	width: 40px;
	border-radius: 50%;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: none !important;

	@media (min-width: 768px) {
		height: 50px;
		width: 50px;
	}

	&.active {
		box-shadow: 0px 4px 16px 0px #FD2E3530;
	}

	&.end {
		background: 0 !important;
	}	
}

.lines {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 33px;
	height: 33px;
	border-radius: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	transition: none !important;

	@media (min-width: 768px) {
		width: 33px;
