	}

	.header-nav {
		li {

			a {

				&::after {
					background-color: #221429;
				}
				&:hover {
					color: #FD2E35 !important;
					&::after, &::before {
						color: #FD2E35 !important;
						background-color: #fd2e35;
					}
				}
				
				&.hvr-line {
					color: #fd2e35 !important;
	
					&::after {
						background-color: #FD2E35 !important;
					}
				}
			}
		}
	}
}


.drop-clr, .drop-white {
	position: absolute;
	right: -4px;
}

.main-links {
	&__item {
		@media (max-width: 767px) {
			padding-bottom: 16px;
		}
	}
}

.op-main-0 {
	opacity: 0;
}

.hvr-m {
	div:last-of-type {
		a {
			margin-bottom: 0 !important;
			padding-bottom: 0;
		}
	}

	&--mb {
		a:last-of-type {
			margin-bottom: 0 !important;
		}
	}
}

.bg-bl-hd {
	@media (max-width: 767px) {
		font-size: 28px !important;
		line-height: 38px !important;
	}
}