.remarks {
    background: #F7F4F2;

    &__item {
        padding: 25px;

        @media (min-width: 768px) {
            padding: 40px;
        }

        @media (min-width: 992px) {
            padding: 60px 100px 50px 100px;
        }
    }

    &__img {
        width: 100%;
        height: auto;
        overflow: hidden;

        img {
            width: 100%;
            max-width: 100%;
            height: auto;
        }
    }
}

.job-detail {
    h2 {
        font-size: 22px;
        line-height: 32px;
        letter-spacing: -0.5px;
        margin-bottom: 20px;
        font-weight: 800;

        @include media-breakpoint-up(md) {
            font-size: 30px;
            line-height: 40px;
            letter-spacing: -1px;
        }

        @include media-breakpoint-up(lg) {
            font-size: 40px;
            line-height: 50px;
            letter-spacing: -2px;
        }

        p {
            font-size: 16px !important;
            line-height: 26px !important;
        }
    }

    li {
        list-style-type: disc;
        font-size: 16px;
        line-height: 26px;
        margin-bottom: 3px;
    }

    &__item {
        margin-bottom: 40px;

        @media (min-width: 768px) {
            margin-bottom: 60px;
        }

        @media (min-width: 992px) {
            margin-bottom: 100px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.remarks-list {
    &__item {
        margin-bottom: 30px;

        &:last-child {
            margin-bottom: 0;
        }

        @media (min-width: 768px) {
            margin-bottom: 0;
            margin-top: 30px;
        }

        &:nth-child(1) {
            margin-top: 0;
        }

        &:nth-child(2) {
            margin-top: 0;
        }

        &:nth-child(3) {
            margin-top: 0;
        }
    }
}
