.car-bnr {
    &__info {
        color: #000000;

        @media (min-width: 768px) {
            color: #ffffff;
        }
    }
}

.clr-w {
    color: #ffffff !important;
}

.popshare-list {
    &__item {
        margin-top: 36px;

        @media (min-width: 992px) {
            margin-top: 47px;
        }

        &:nth-child(1) {
            margin-top: 0;
        }

        &:nth-child(2) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }
    }
}

.bg-card {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    position: relative;
    transition: 0.2s ease-in-out;

    &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 100%;
        height: 35%;
        background: linear-gradient(180deg, rgba(34, 20, 41, 0) 0%, rgba(34, 20, 41, 0.9) 100%);
        z-index: 1;

    }

    .reveal {
        min-height: 200px;
    @media (min-width: 768px) {
        min-height: 330px;
    }

    @media (min-width: 992px) {
        min-height: 450px;
    }

    }

    * {
        position: relative;
        z-index: 2;
    }

  
}

.location-list {
    justify-content: space-between;

    &__item {
        width: calc(50% - 8px);
        margin-top: 30px;

        @media (min-width: 992px) {
            width: calc(25% - 8px);
            margin-top: 16px;
        }

        &:nth-child(1) {
            margin-top: 0;
        }

        &:nth-child(2) {
            margin-top: 0;
        }

        &:nth-child(3) {
            @media (min-width: 992px) {
                margin-top: 0;
            }
          
        }

        &:nth-child(4) {
            @media (min-width: 992px) {
                margin-top: 0;
            }
        }
        
        .location {
            height: 100%;
            position: relative;
            overflow: hidden;

            &__info {
                
                margin-top: 15px;

                @media (min-width: 768px) {
                    margin-top: 25px;
                }
            }


            .loc-img {
               
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &:hover {
                &::before {
                    bottom: 0;
                    opacity: 1;
                }

                .reveal {
                    img {
                        transform: scale(.95)!important;
                    }
                }
            }
        }
    }
}

.open-ticket-list {
    &__item {
        a {
            padding: 20px 30px 20px 0;
            border-bottom: 1px solid rgba(135, 124, 136, 0.4);
            position: relative;

            &::after {
                position: absolute;
                content: "";
                display: block;
                margin-right: 8px;
                width: 8px;
                height: 8px;
                border-top: 2px solid #877C88;
                border-left: 2px solid #877C88;
                transform: translateY(-50%) rotate(135deg);
                right: 0;
                top: 50%;

            }

            h6 {
                padding-right: 25px;
            }

            span {
                float: right;
                text-align: right;
            }

            h6, span {
               transition: 0.1s ease-in-out;
            }
        }
        &:last-of-type {
            a {
                border-bottom: 0;
            }
        }

        &:hover {
            h6, span {
                color: #FD2E35;
            }

           a {
               &::after {
                border-color: #FD2E35;
               }
           }
        }
    }
}

.accordion {
    padding: 15px 0;
    position: relative;

    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 125px;
        height: 2px;
        background: #877C88;
    }

    @media (min-width: 768px) {
        padding: 20px 0;
    }

    @media (min-width: 992px) {
        padding: 30px 0;
    }

    &__head {
        cursor: pointer;
    }

    &__body {
        display: none;
    }

    &.active {
        .accordion__body {
            display: block;
        }
    }
}

.accordions {
    .accordion {
        &:last-of-type {
            border: 0;
            padding-bottom: 0;
        }
        
    }
}

.pr-12 {
    padding-right: 12px;
}

.ml-6 {
    @media (min-width: 768px) {
        margin-left: -6px;
    }
}

.random-pics {
    &__item {
        height: inherit;
        margin-top: 6px;

        @media (min-width: 768px) {
            height: inherit;
        }

        .reveal {
            @media (min-width: 768px) {
                height: 100%;
            }
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &:nth-child(1) {
            width: calc(60% - 3px);
            margin-top: 0;
          
        }

        &:nth-child(2) {
            width: calc(40% - 3px);
            margin-top: 0;
        }

        &:nth-child(3) {
            width: calc(45% - 3px);
        }

        &:nth-child(4) {
            width: calc(55% - 3px);
        }

        &:nth-child(5) {
            width: calc(35% - 3px);
        }

        &:nth-child(6) {
            width: calc(65% - 3px);
        }
    }
}

.car-bnr-img {
    max-width: 100%;
    height: auto;
  
    object-fit: cover;


}

.white-paper-cards {
    .img {
        @media (min-width: 768px) {
            height: calc(100% - 48px) !important;
            margin-top: auto;
        }
    }

    .flip-img {
        @media (min-width: 768px) {
            display: flex;
            flex-direction: column;
        }
    }
}




.head-text {
    .quote span {
        @media (min-width: 768px) and (max-width: 991px) {
            font-size: 50px !important;
            line-height: 60px !important;
        }
    }
}


.js-apply-now {
    @media (max-width: 767px) {
        position: fixed;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 999;
       
       
    }

    .web-btn {
        @media (max-width: 767px) {
            background-color: #FD2E35 !important;
            color: #ffffff !important;
        }
    }

    &.active {
        @media (max-width: 767px) {
           position: relative !important;
           bottom: auto;
           left: auto;
           transform: none;
        }

        .web-btn {
            background-color: #ffffff !important;
            color: #FD2E35 !important;
        }
    }
}