.three-list {
    &__el {
       

        @media (max-width: 767px) {
            margin-bottom: 24px;
        }

        &:last-child {
            @media (max-width: 767px) {
                margin-bottom: 0;
            }  
        }

        @media (min-width: 768px) {
            margin-top: 30px;
        }

        @media (min-width: 992px) {
            margin-top: 48px;
        }

        &:nth-child(1),&:nth-child(2),&:nth-child(3) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }

        * {
            @media (min-width: 768px) {
                padding-right: 25px;
            }

            @media (min-width: 992px) {
                padding-right: 50px;
            }

            @media (min-width: 1200px) {
                padding-right: 60px;
            }
        }
       
    }
}

.thing-list {
    &__item {
       
        @media (min-width: 768px) {
            margin-top: 30px;
        }


        &:nth-child(1),&:nth-child(2),&:nth-child(3) {
            @media (min-width: 768px) {
                margin-top: 0;
            }
        }

        .disc {
            padding: 20px;
            background-color: #ffffffff;
            height: 100%;

            @media (min-width: 768px) {
                padding: 24px;
                
            }

            &__stroke {
                width: 24px;
                height: 4px;
                background: #FD2E35;
                margin-bottom: 12px;
                display: inline-block;
            }
        }
    }
}

.fade-img-list {
    &__item {
        
        &:nth-child(1) {
            width: 100%;
            margin-bottom: 10px;
            @media (min-width: 768px) {
                width: 60%;
                padding-right: 6px;
                margin-bottom: 0;
            }
        }

        &:nth-child(2) {
            width: 100%;
            @media (min-width: 768px) {
                width: 40%;
            }
        }

        .img {

            @media (max-width: 767px) {
                width: 100%;
                max-width: 100%;
                height: auto;
            }

            @media (min-width: 768px) {
                width: 100%;
                max-width: 100%;
                height: 400px;
                object-fit: cover;
                object-position: center;
            }
        }
    }
}

.faq-el {
    background-color: #ffffff;
    padding: 20px;

    @media (min-width: 768px) {
        padding: 45px;
    }

    @media (min-width: 992px) {
        padding: 70px;
    }

    @media (min-width: 1200px) {
        padding: 95px;
    }
}

.faq-ac {
    &__item {
        margin-bottom: 10px;

        @media (min-width: 768px) {
            margin-bottom: 20px;
        }


        @media (min-width: 992px) {
            margin-bottom: 30px;
        }


       

        &:last-child {
            margin-bottom: 0;
        }
    }

    &--2 {
        .faq-ac__item:nth-last-child(-n+2) {
            margin-bottom: 0;
        }

        .faq-ac__item {
            &:nth-child(even) {

                @media (min-width: 768px) {
                    padding-left: 30px;
                }

                @media (min-width: 1200px) {
                    padding-left: 40px;
                }
               
            }

            &:nth-child(odd) {
                @media (min-width: 768px) {
                    padding-right: 30px;
                }

                @media (min-width: 1200px) {
                    padding-right: 40px;
                }
            }
        }
    }
}

.f-accordion {
    &__head {
       
        position: relative;
        padding-right: 25px;
        cursor: pointer;

        @media (min-width: 768px) {
            padding-right: 40px;
        }

        @media (min-width: 992px) {
            padding-right: 50px;
        }

        &::after {
            position: absolute;
            content: "";
            top: 14px;
            right: 0;
            width: 16px;
            height: 2px;
            background-color: #FD2E35;
        }

        &::before {
            position: absolute;
            content: "";
            top: 14px;
            right: 0;
            width: 16px;
            height: 2px;
            background-color: #FD2E35;
            transform: rotate(90deg);
            transition: 0.3s ease-in-out;
        }
    }

    &__body {
        padding-right: 25px;
        padding-top: 10px;
        padding-bottom: 15px;

        @media (min-width: 768px) {
            padding-right: 40px;
            padding-bottom: 25px;
        }

        @media (min-width: 992px) {
            padding-right: 50px;
        }

        display: none;

    }

    &.active {
        .f-accordion__head {
            &::before {
                transform: rotate(0deg);
            }
        }
    }
}