.title {
	font-size: rem(20px);
	line-height: rem(30px);

	@include media-breakpoint-up(lg) {
		font-size: rem(30px);
		line-height: rem(40px);
	}
}

.fnt-78 {
	font-size: 32px;
	line-height: 42px;
