.process {
    .step-list {
        &__item {
            margin-bottom: 24px;

            @media (min-width: 768px) {
                margin-bottom: 30px;
            }

            @media (min-width: 992px) {
                margin-bottom: 40px;
            }

        }
    }
}

.process-step {
    &__item {
        margin-bottom: 60px;

        @media (min-width: 768px) {
            margin-bottom: 90px;
        }

        @media (min-width: 992px) {
            margin-bottom: 120px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.sticky-desktop-tab {
    @media (min-width: 768px) {
        position: sticky;
        width: 100%;
        top: 180px
    }
}