footer {
    background: #F9F6F5;
    padding: 40px 0 24px 0;

    @media (min-width: 768px) {
        padding: 80px 0;
    }

    @media (min-width: 992px) {
        padding: 120px 0;
    }

    &.fixed {
        position: absolute;
        bottom: 0;
        width: 100%;
        // height: 100%;
        left: 0;
        // transform: translateY(100%);
       
    }

    .rd-stroke {
        padding-left: 0 !important;
        &::before {
            display: none !important;
        }
    }
}

.footer-logo-links {
    &__item {
        margin-right: 10px;
        margin-left: 10px;

        @media (min-width: 768px) {
            margin-left: 0;
        }

        @media (min-width: 992px) {
            margin-right: 30px;
        }

        &:last-child {
            margin-right: 0;
        }

        a {
            color: #877C88;
        }
    }
}

.footer-links {

    @media (min-width: 768px) {
        padding-left: 45px;
    }

    @media (min-width: 992px) {
        padding-left: 70px;
    }

    @media (max-width: 767px) {
        padding-right: 25px;
        padding-left: 0;
    }

    &__item {
        margin-bottom: 7px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}


.hvr-link {
    position: relative;
    transition: transform 0.3s !important;

    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        background-color: #FD2E35;
        transform: scaleX(0);
        transform-origin: bottom left;

        transition: transform 0.3s;
    }

    // &:after {
    //     content: '';
    //     position: absolute;
    //     bottom: 0;
    //     right: 0;
    //     left : 50%;
    //     height: 1px;
    //     background-color: #FD2E35;
    //     transform: scaleX(0);
    //     transform-origin: bottom right;
    //     transition: transform 0.3s;
    // }

    &:hover {
        color: #FD2E35;

        &:before {
            transform: scaleX(1);
        }

        // &:after {
        //     transform: scaleX(1);
        // }
    }
}


.footer-nav-links {
    .hvr-link {
        &::before, &::after {
            bottom: 0 !important;
        }
    }
}


.hvr-link-div {
    h6 {
        transition: 0.3s ease-in-out;
    }

    &:hover {
        h6 {
            color: #FD2E35;
        }
    }
}
.ft-social-links {
    a {
        margin-bottom: 30px;

        @media (min-width: 768px) {
            margin: 0 20px 20px 20px;
        }
    }
}

.link-linkedin {
    transition: 0.2s ease-in-out;
    &:hover {
        @media (min-width: 992px) {
            color: #0077b5;
        }
        
    }
}

.link-insta {
    transition: 0.2s ease-in-out;
    &:hover {
        @media (min-width: 992px) {
            color: #8a3ab9;
        }
    }
}

.link-fb {
    transition: 0.2s ease-in-out;
    &:hover {
        @media (min-width: 992px) {
            color: #4267B2;
        }
    }
}

.link-twt {
    transition: 0.2s ease-in-out;
    &:hover {
        @media (min-width: 992px) {
            color: #1DA1F2;
        }
    }
}

.menu-ftr {
    &__links {
        a {
            margin-right: 10px;

            @media (min-width: 1300px) {
                margin-right: 20px;
            }

         
        }
    }
}

.js-page-scroll-top {
    position: fixed;
    bottom: 10px;
    right: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    opacity: 0;
    transition: 0.3s ease-in-out;
    border-radius: 50%;
    cursor: pointer;

    @media(min-width: 768px) {
        width: 32px;
        height: 32px;
        bottom: 45px;
    }

    img {
        width: 100%;
        height: 100%;
        box-shadow: 0 4px 16px 0 #fd2e3530;
        border-radius: 50%;
    }

    &.active {
        opacity: 1;
       
    }
}

