/*= input focus effects css
=========================== */
:focus{outline: none;}
.in-animate {
    i {
        position: relative;
    }
}

.input{position: relative;
    border: 0 !important;
  

    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid rgba(34,20,41,.14);
        pointer-events: none;
    }

    input {
        color: #221429 !important;
    }

    .bx {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        box-shadow: 0px 2px 0px 0px #00000021 !important;
        transition: 0.3s ease-in-out;
    }
} /* necessary to give position: relative to parent. */
.in-animate{transition: 0.4s;}
.in-animate ~ .focus-border:before,
.in-animate ~ .focus-border:after{content: ""; position: absolute; top: 0; right: 0; width: 0; height: 1px; background-color: #fd2e35; transition: 0.2s; transition-delay: 0.2s;}
.in-animate ~ .focus-border:after{top: auto; bottom: 0; right: auto; left: 0; transition-delay: 0.6s;}
.in-animate ~ .focus-border i:before,
.in-animate ~ .focus-border i:after{content: ""; position: absolute; top: 0; left: 0; width: 1px; height: 0; background-color: #fd2e35; transition: 0.2s;}
.in-animate ~ .focus-border i:after{left: auto; right: 0; top: auto; bottom: 0; transition-delay: 0.4s;}
.in-animate:focus ~ .focus-border:before,
.in-animate:focus ~ .focus-border:after{width: 100%; transition: 0.2s; transition-delay: 0.6s;}
.in-animate:focus ~ .focus-border:after{transition-delay: 0.2s;}
.in-animate:focus ~ .focus-border i:before,
.in-animate:focus ~ .focus-border i:after{height: 100%; transition: 0.2s;}
.in-animate:focus ~ .focus-border i:after{transition-delay: 0.4s;}
.in-animate:focus ~ .bx {opacity: 0;}


input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}
/*= input focus effects css End
=============================== */