.sec-paper-detail {
    img {
        max-width: 100%;
        height: auto;
    }
}



.paper-detail {
    p,.rd-stroke {
        margin-bottom: 20px;

        @media (min-width: 992px) {
            margin-bottom: 30px;
        }
    }

    p,a {
        @media (max-width: 767px) {
            font-size: 16px !important;
            line-height: 26px !important;
        }
    }

   

    h2 {
        font-size: 22px;
        line-height: 32px;
        font-weight: 800;
        margin-bottom: 25px;
        letter-spacing: -1px;
    
        @include media-breakpoint-up(md) {
            font-size: 30px;
            line-height: 40px;
            margin-bottom: 30px;
            letter-spacing: -1px;
        }
    
        @include media-breakpoint-up(lg) {
            font-size: 40px;
            line-height: 50px;
            margin-bottom: 40px;
            padding-right: 40%;
            letter-spacing: -2px;
        }
    }

    .rd-stroke {

        font-size: 16px !important;
            line-height: 26px !important;
            letter-spacing: normal !important;
        @media (min-width: 768px) {
            font-size: 18px !important;
            line-height: 30px !important;
            letter-spacing: normal !important;
        }
        &::before {
            top: 3px !important;

            @media (min-width: 768px) {
                top: 8px !important;
            }
        }
    }

  

    h3 {
        font-size: 20px;
        line-height: 30px;
        font-weight: 800;
        margin-bottom: 20px;
        display: flex;
        letter-spacing: -0.5px;
        margin-top: 20px;

        @include media-breakpoint-up(md) {
            font-size: 25px;
            line-height: 35px;
            margin-bottom: 25px;
            margin-top: 50px;
            letter-spacing: -1px;
        }
    
        @include media-breakpoint-up(lg) {
            font-size: 28px;
            line-height: 38px;
            margin-bottom: 30px;
            letter-spacing: -0.5px;
            margin-top: 80px;
        }
    }

    h6, h4 {
        font-size: 16px;
        line-height: 26px;
        font-weight: 700;
        margin-bottom: 20px;
        display: flex;
        position: relative;
      


        @include media-breakpoint-up(md) {
            font-size: 16px;
            line-height: 26px;
            margin-bottom: 20px;
        }
    
        @include media-breakpoint-up(lg) {
            font-size: 18px;
            line-height: 28px;
            margin-bottom: 20px;
        }
    }

    &__head {
        .paper-head-info {
            margin-bottom: 40px;

            @media (min-width: 768px) {
                margin-bottom: 50px;
            }

            @media (min-width: 992px) {
                margin-bottom: 80px;
            }
        }

        margin-bottom: 40px;

        @media (min-width: 768px) {
            margin-bottom: 50px;
        }

        @media (min-width: 992px) {
            margin-bottom: 80px;
        }
    }

    .points-div {
        margin-bottom: 30px;

        @media (min-width: 992px) {
            margin-bottom: 30px;
        }

        p:last-of-type {
            margin-bottom: 0;
        }
    }

    p,a,span {
        @media (min-width: 768px) {
            font-size: 18px !important;
            line-height: 30px !important;
            letter-spacing: normal !important;
        }
    }

    .content-container {
        h3:first-of-type {
            margin-top: 0 !important;
        }
    }
}

.paper-disc-list {
    &__item {
        margin-bottom: 40px;

        @media (min-width: 768px) {
            margin-bottom: 50px;
        }

        @media (min-width: 992px) {
            margin-bottom: 80px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.rd-stroke {
    position: relative;
    padding-left: 15px;

    @media (min-width: 768px) {
        padding-left: 24px;
    }

    &::before {
        position: absolute;
        content: "";
        top: 8px;
        left: 0;
        width: 5px;
        height: 42px;
        background: #FD2E35;

        @media (min-width: 768px) {
            top: 10px;
        }
    }
}

.wpdm-img {
    width: 100%;
    max-width: 100%;
    height: auto;
}
