	color                  : $primary;
	-webkit-font-smoothing : antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering         : optimizeLegibility;
	background             : $bodymain;
}


@function tracking( $target ){
	@return ($target / 1000) * 1em;
  }
  
  @mixin tracking( $target ){
	letter-spacing: tracking( $target );
  }

