//img hover
.img-hvr {
	overflow: hidden;
	transition: 0.4s ease-in-out;

	img {
		transition: 0.4s ease-in-out;
	}

	&:hover {
		transform: scale(0.95);
		img {
			transform: scale(1.1);
		}
	}
}

.blue-hvr {
	&__item {
		position: relative;
		overflow: hidden;

		* {
			position: relative;
			z-index: 5;
		}

		&::before {
			position: absolute;
			content: "";
			top: 50%;
			left: 50%;
			width: 100%;
			height: 100%;
			background: #221429;
			transition: 0.8s ease-in-out;
			transform: translate(-50%, -50%) scale(0);

		}

		&:hover {
			&::before {
				// width: 100%;
				transform: translate(-50%, -50%) scale(1) ;
			}

			* {
				color: #ffffff !important;
			}
		}
	}

	&:hover {
		.blue-hvr__item {
			&::before {
				// width: 100%;
				transform: translate(-50%, -50%) scale(1) ;
			}

			* {
				color: #ffffff !important;
			}
		}
	}
}

.image-text-list {
