					}
				}

				.flip-text {
					@media (min-width: 768px) {
						order: 1;
					}
				}

				.row:first-of-type {
					justify-content: space-between;
				}
			}
		}
	}

	&__item {
		&:hover {
		
			.hover-img {
				transform: scale(0.95) !important;
			}
			img {
				transform: translate(0px, 0px) scale(1.08) !important;	
			}
		}
	}
}

