.lollypop-tabs {
	-ms-overflow-style: none;  
	scrollbar-width: none; 
	flex-wrap: nowrap !important;
	overflow-x: auto;

		@media (max-width: 767px) {
		display: flex;
		flex-direction: row !important;
		flex-wrap: nowrap;
		overflow-x: auto;
		overflow-y: hidden;
	}

	&::-webkit-scrollbar {
		display: none;
	}

	.nav-link {
		background-color: 0 !important;
		// background: none;
		// color: rgba(34, 20, 41, 0.4);
		text-align: left;
		position: relative;
		padding: 0;
		text-transform: capitalize;
		white-space: nowrap;

		@media (max-width: 767px) {
			font-size: 20px !important;
		}
		
		&::after {
			position: absolute;
			content: "";
			bottom: -10px;
			left: 0;
			width: 0;
			height: 4px;
			background: #221429;
			opacity: 0;
			transition: 0.3s ease-in-out;
			
		}

		&.active {
			color: #221429 !important;
			background: 0 !important;
			

			&::after {
				opacity: 1;
				width: 32%;
			}
		}

		&:hover {
			color: #221429;
		}
	}

	&--vertical {
		.nav-link {
			display: inline-block;
			margin-right: 15px;

			@media (min-width: 768px) {
				margin-bottom: 20px;
				margin-right: 45px;
			}

			@media (min-width: 992px) {
				margin-right: 50px;
			}

			&.active {
				color: #fd2e35 !important;

				&::after {
					background: #fd2e35 !important;
				}
			}
		}
	}
}

