		height: 15px;
		border-top: 2px solid #FD2E35;
		border-left: 2px solid #FD2E35;
		transform: rotate(135deg);
	}
}

.slick-arrow {
	cursor: pointer;
}

.slick-arrow.slick-disabled {
	display: none !important;
	visibility: hidden;
	opacity: 0;
}

.slick-slider {
	&:hover {
		.slick-arrow {
			opacity: 0.6;
		}
	}
}

.prev-slide {
	position: absolute;
	top: 40%;
	left: 15px;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: #ffffff;
	transform: translateY(-50%);
	
	display: flex !important;
	align-items: center;
	justify-content: center;
	z-index: 9;
	transition: 0.2s ease-in-out;

	@media (min-width: 768px) {
		opacity: 0;
		width: 50px;
		height: 50px;
	}

	.ar {
		display: block;
		margin-left: 8px;
		width: 15px;
		height: 15px;
		border-top: 2px solid #FD2E35;
		border-left: 2px solid #FD2E35;
		transform: rotate(-45deg);
	}
}


.quote {
	span {
		transition: 0.6s ease-in-out;
	}

	&__item {
		// transform: scale(0);
		position: fixed;
		top: -100%;
		opacity: 0;
		

		&.active {
			// transform: scale(1);
			opacity: 1;
			position: relative;
			display: block;
			animation: fadeIn 1s infinite alternate;
		}
	}
}

@keyframes fadeIn { 
	from { opacity: 0; } 
	}

