.py-140 {
	padding : {
		top   : rem(40px);
		bottom: rem(40px);
	}
 

	@include media-breakpoint-up(md) {
		padding : {
			top   : rem(80px);
			bottom: rem(80px);
		}
	}

	@include media-breakpoint-up(lg) {
		padding : {
			top   : rem(140px);
			bottom: rem(140px);
		}
	}
}


.py-230 {
	padding : {
		top   : rem(80px);
		bottom: rem(40px);
	}
 

	@include media-breakpoint-up(md) {
		padding : {
			top   : rem(100px);
			bottom: rem(80px);
		}
	}

	@include media-breakpoint-up(lg) {
		padding : {
			top   : rem(120px);
			bottom: rem(140px);
		}
	}
}



.py-100 {
	padding : {
