.lp-index {
	&__heading {
		margin-bottom: 1.8rem;

		@include media-breakpoint-up(lg) {
			margin-bottom: 3.8rem;
		}
	}
}

.lp-number-list {
	list-style-type: none;
	display        : flex;
	margin-bottom  : rem(30px);

	&__item {
		margin-right: 1.5rem;

		@include media-breakpoint-up(md) {
			margin-right: 2.5rem;
		}

		@include media-breakpoint-up(lg) {
			margin-right: 4.5rem;
		}
	}
}

.lp-number-title {
	display: inline-block;
	@extend .fnt-800;
}

// our portfolio
.lp-portfolio {
	&__cnt {
		@include media-breakpoint-up(lg) {
			padding-right: 4.5rem;
		}

		@include media-breakpoint-up(xl) {
			padding-right: 2rem;
			padding-left : 2rem;
		}

		@include media-breakpoint-up(xxl) {
			padding-left : 3.2rem;
			padding-right: 2rem;
		}
	}
}

// LP OUR SERVICES
.lp-block-list {
	margin-bottom: 0;

	&__item {

		padding : {
			top   : rem(16px);
			bottom: rem(16px);
		}

		@include media-breakpoint-up(xl) {
			padding : {
				// top   : rem(36px);
				bottom: rem(20px);
			}
		}

		&:last-child {
			padding-bottom: 0;
		}
	}
}

.lp-list {
	padding-left: 0.8rem;

	&__item {
		position     : relative;
		padding-left : 1rem;
		margin-bottom: 5px;

		&::before {
			content      : "";
			position     : absolute;
			background   : rgba(144, 138, 148, 0.57);
			height       : rem(4px);
			width        : rem(4px);
			border-radius: 50%;
			top          : 10px;
			left         : 0;
		}
	}
}

// blogs
.lp-hero-box {
	background: $bg-f9f6;

	&--blog {
		@include media-breakpoint-down(xl) {
			padding: rem(20px);
		}

		@include media-breakpoint-up(xl) {
			padding: {
				top   : rem(70px);
				left  : rem(50px);
				bottom: rem(65px);
				right : rem(70px);
			}
		}

		@include media-breakpoint-up(xxl) {
			padding: {
				top   : rem(70px);
				left  : rem(70px);
				bottom: rem(65px);
				right : rem(100px);
			}
		}
	}
}

.lp-publish {
	display: inline-block;
}

.lp-card-group {
	@include media-breakpoint-up(xl) {
		padding: {
			left : 1rem;
			right: 1rem;
		}
	}

	@include media-breakpoint-up(xxl) {
		padding: {
			right: 3rem;
		}
	}
}

.lp-card-article {

	@include media-breakpoint-down(lg) {
		padding: rem(20px);
		margin-bottom: rem(15px);
	}

	@include media-breakpoint-up(lg) {
		margin-bottom: rem(50px);
	}

	&:last-child {
		margin-bottom: 0;
	}

	&__heading {
		@include media-breakpoint-up(xl) {
			font-size  : rem(24px);
			line-height: rem(34px);
		}
	}
}

.lp-blg-des {
	color: $text-877;

	@include media-breakpoint-up(xxl) {
		max-width: 350px;
	}
}

.reveal {
	visibility: hidden;
	position: relative;
	width: 100%;
	// height: 100%;
	overflow: hidden;

	img {
		width: 100%;
		height: auto;
	}
}
  
.sec-scroll {
	width: 30%;
	transition: 0.8s ease-in-out;
	opacity: 1;
}


.lp-video-wrap {
	.plyr__controls {
		display: none !important;
	}
}

.sec-footer-trans {
	position: relative;

	&::after {
		position: absolute;
		content: "";
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50%;
		background: #F9F6F5;
	z-index: 0;
	}
}

.z-1 {
	z-index: 1;
	position: relative;
}

.car-bnr-img {
	height: 240px;
	max-width: 100%;

	@media (min-width: 992px) {
		height: 300px;
		height: auto;
	}

	@media (min-width: 1400px) {
		height: auto;
	}
}

.culture-slider-nav {
	@media (max-width: 767px) {
		display: none !important;
		opacity: 0;
		visibility: hidden;
	}

	&__item {
		margin-right: 30px;
		transition: 0.2s ease-in-out;
		cursor: pointer;

		&:last-child {
			margin-right: 0;
		}

		&.active {
			color: #FD2E35;
		}
	}
}

.active-slide {
	.slick-current {
		* {
			color: #FD2E35;
		}
	}
}

.hvr-line {
	position: relative;
	transition: 0.3s ease-in-out;

	&::after {
		position: absolute;
		content: "";
		bottom: -5px;
		left: 0;
		width: 25%;
		height: 1px;
		background: #FD2E35;
		transition: 0.3s ease-in-out;
	}

	&--black {
		&::after {
			background: #221429;
		}

		&:hover {
			color: #221429;
		}
	}

	&::before {
		display: none !important;
		opacity: 0;
	}

	&:hover {
		color: #FD2E35;

		&::after {
			width: 100%;
		}
	}
}

.hvr {
	position: relative;
	transition: 0.3s ease-in-out;

	&::after {
		position: absolute;
		content: "";
		bottom: -5px;
		left: 0;
		width: 0;
		height: 1px;
		background: #FD2E35;
		transition: 0.3s ease-in-out;
	}

	&::before {
		display: none !important;
		opacity: 0;
	}

	&:hover {
		color: #FD2E35;

		&::after {
			width: 100%;
		}
	}

	&--white {
		&::after {
			background: #ffffff;
		}

		&:hover {
			color: #ffffff;
		}
	}
}

.bg-wrapper--purple {
	position: relative;
	background-color: #221429;
	transform: translateY(30%);
	transition: 0.8s ease-in-out;

	&.active {
		transform: translateY(0);
	}
}

.data-scroll {
	opacity: 0;
	transform: translateY(20px);
	transition: 0.5s ease-in-out;

	@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
		opacity: 1;
		transform: translate(0);
	}
}

.indutstry-list {
	&__item {
		margin-top: 30px;

		@media (min-width: 768px) {
			margin-top: 50px;
		}

		@media (min-width: 992px) {
			margin-top: 70px;
		}

		&:nth-child(1) {
			margin-top: 0;
		}

		&:nth-child(2) {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}

		p {
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}

.home-whitepaper {
	background: #F9F6F5;
	padding: 15px;

	@media (min-width: 768px) {
		padding: 30px 25px 40px 25px;
	}

	@media (min-width: 992px) {
		padding: 56px 48px 70px 48px;
	}

	&__img {
		width: 100%;

		@media (max-width: 767px) {
			margin-bottom: 20px;
		}

		img {
			width: 100%;
			height: auto;
			object-fit: cover;

			@media (min-width: 768px) {
				height: 100%;
			}
		}

		@media (min-width: 768px) {
			width: 200px;
		}

		@media (min-width: 992px) {
			width: 265px;
		}
	}

	&__info {
		width: 90%;

		@media (min-width: 768px) {
			width: calc(100% - 200px);
			padding: 0 25px;
		}

		@media (min-width: 992px) {
			padding: 0 35px;
			width: calc(100% - 265px);
		}
	}
}

.home-blog {
	&__item {
		margin-top: 40px;

		@media (min-width: 768px) {
			margin-top: 40px;
		}

		@media (min-width: 992px) {
			margin-top: 60px;
		}

		&:nth-child(1) {
			margin-top: 0;
		}

		&:nth-child(2) {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}
	}
}

.col-mb-2 {
	@media (max-width: 767px) {
		column-count: 2;
	}
}

//ANimation
.scroll-image {
	opacity: 0;
	transform: translateX(-100%);
	transition: 0.7s ease-in-out;

	&.active {
		opacity: 1;
		transform: translateX(0);
	}
}

.client-cards {
	padding: 40px 15px 15px 15px;
	background-color: #ffffff;
	height: 100%;

	@media (min-width: 768px) {
		padding: 50px 20px 20px 20px;
	}

	@media (min-width: 992px) {
		padding: 60px 27px 27px 27px;
	}
}

.home-artical {
	.revealnone {
		@media (min-width: 768px) {
			margin-top: 44px;
			height: calc(100% - 44px) !important;
		}
	}
}

.home-artical-img {
	@media (max-width: 767px) {
		width: 100%;
		height: auto;
	}

	@media (min-width: 768px) {
	
		width: 100%;
		max-width: 100%;
		min-height: calc(100% - 44px);
		height: auto;
		object-fit: cover;
	}
}

section {
	.home-artical-list {
		margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 45px;
		}

		@media (min-width: 992px) {
			margin-bottom: 70px;
		}

		&:last-of-type {
			margin-bottom: 0;
		}

		&__item {
			margin-bottom: 20px;

			@media (min-width: 768px) {
				margin-bottom: 30px;
			}
	
			@media (min-width: 992px) {
				margin-bottom: 40px;
			}
	
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}


.awrad-list {
	&__item {
		@media (max-width: 767px) {
			margin-bottom: 25px;
		}

		&:last-child {
			@media (max-width: 767px) {
				margin-bottom: 0;
			}
		}

		@media (min-width: 768px) {
			margin-top: 50px;
		}

		@media (min-width: 1200px) {
			margin-top: 70px;
		}

		&:nth-child(1), &:nth-child(2), &:nth-child(3) {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}
	}
}

.award-info {
	overflow: hidden;
	transition: 0.4s ease-in-out;
	img {
		transition: 0.4s ease-in-out;
	}

	&:hover {
		transform: scale(0.95);
		img {
			transform: scale(1.08);
		}
	}
}