.blog-main-img {
	width: 100%;
	min-height: 250px;
	@media (max-width: 767px) {
		background-color: #ffffff;
		width: calc(100% + 40px);
		margin-left: -20px;
	}


	@media (min-width: 768px) {
		min-height: 350px;
	}

	@media (min-width: 992px) {
		min-height: 500px;
	}

	img {
		width: 100%;
		min-height: auto;
		max-height: 100%;
		object-fit: cover;

	}

	.blog-info {
		@media (max-width: 767px) {
			padding: 24px 20px 35px 20px;
		}
	
		@media (min-width: 768px) {
			position: absolute;
			bottom: -70px;
			right: 70px;
			z-index: 2;
			padding: 25px;
			background-color: #ffffff;
		}

		@media (min-width: 992px) {
			padding: 40px;
		}
	}
}

.blog-list {
	&__item {
		margin-bottom: 50px;

		@media (min-width: 768px) {
			margin-bottom: 50px;
		}

		@media (min-width: 992px) {
			margin-bottom: 60px;
		}

		@media (min-width: 1200px) {
			margin-bottom: 80px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	&--second {
		.blog-img {
			img {
				@media (max-width: 767px) {
					max-width: 70% !important;
				}
			}
		}
	}

	&--topmost {
		.blog-info {
			@media (min-width: 768px) {
				padding: 0 15px 0 12px !important;
			}
		}

		.blog-img {

			.reveal {
				@media (min-width: 768px) {
					width: 100% !important;
					height: 100% !important;
				}
			}
			
			img {
				@media (min-width: 768px) {
					width: 100% !important;
					height: 100% !important;
				}
			}
		}
		
	
	}

	&--whitepaper {
		@media (max-width: 767px) {
			background-color: #ffffff;
		}

		.blog-img {
			height: auto !important;
			img {
				@media (max-width: 767px) {
					max-width: 70% !important;
				}
			}
		}
	}

	
}

.article-div  {
	margin-bottom: 50px;

	@media (min-width: 768px) {
		margin-bottom: 50px;
	}

	@media (min-width: 992px) {
		margin-bottom: 60px;
	}

	@media (min-width: 1200px) {
		margin-bottom: 80px;
	}

	&:last-of-type {
		margin-bottom: 0;
	}

}

.blog {
	.blog-img {
		@media (max-width: 767px) {
			min-height: 179px;
		}

		overflow: hidden;

		min-height: 202px;
		
		.reveal {
			height: 100%;
		}

		img {
			width: 100% ;
			max-width: 100%;
			height: 100%;
			min-height: 100%;
			object-fit: cover;
			max-width: 100% !important;

			// @media (max-width: 767px) {
			// 	height: 100% !important;
			// 	width: calc(100% + 40px) !important;
			// 	margin-left: -20px;
			// 	overflow: hidden;
			// }
		}
	}

	.blog-info {
		@media (min-width: 768px) {
			padding-left: 23px;
		}

		span {
			display: inline-block;
			margin-right: 8px;
			color: #221429;
		}

		// p {
		// 	color: #877C88 !important;
		// }
	}

	.blog-titile {
		font-size: 24px;
		line-height: 34px;
		letter-spacing: -0.5px;

		@media (min-width: 768px) {
			font-size: 28px;
			line-height: 38px;
		}

		@media (min-width: 992px) {
			font-size: 30px;
			line-height: 40px;
			letter-spacing: -1px;
		}
	}

	&:hover {
		
		.reveal img {
			
				transform: translate(0px, 0px) scale(1.08) !important;
			
		}
	}
}

.main-blog-title {
	font-size: 24px;
		line-height: 34px;
		letter-spacing: -1px;

		@media (min-width: 768px) {
			font-size: 26px;
			line-height: 36px;
			letter-spacing: -1px;
		}

		@media (min-width: 992px) {
			font-size: 28px;
			line-height: 38px;
			
		}
}


.popshot {
	&__img {
		position: relative;
		.video-poster {
			width: 100%;
			height: 100%;
			object-fit: cover;

			@media (max-width: 767px) {
				height: auto;
			}
		}

		&:hover {
			.icon-play {
				&:before {
					color: #ffd522;
				}
			}
		}

		height: 215px;

		@media (min-width: 768px) {
			height: 300px;
		}

		@media (min-width: 992px) {
			height: 358px;
		}

		.reveal {
			height: 100%;
		}

	}

	.icon-insta, .icon-web {
		font-size: 20px;
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);

		@media (min-width: 992px) {
			font-size: 24px;
		}
	}

	&__info {
		@media (min-width: 768px) {
			display: flex;
			flex-direction: column;
			height: 100%;
		
		}

		.mb-40 {
			@media (max-width: 767px) {
				margin-bottom: 20px !important;
			}
		}
	}
}

.play-icon {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	pointer-events: none;
	z-index: 2;

	.icon-play {
		font-size: 30px;

		@media (min-width: 992px) {
			font-size: 55px;
		}

		&::before {
			transition: 0.1s ease-in-out;
		}
	}
}

.white-paper {
	padding: 24px 12px 24px 12px;

	@media (min-width: 768px) {
		padding: 40px 40px 50px 40px;
	}

	@media (min-width: 992px) {
		padding: 40px 65px 65px 65px;
	}

	.blog-img {
		@media (min-width: 768px) {
			padding-right: 8px;
		}

		img {
			@media (max-width: 767px) {
				max-width: 70% !important;
			}
		}
	}

	&__info {
		@media (min-width: 768px) {
			padding: 0 4px 0 4px;
		}
	}
}

.mb-img {
	@media (max-width: 767px) {
		margin-bottom: 20px;
	}
}

// Blog Detail
.blog-auth {
	@media (min-width: 768px) {
		position: sticky;
		top: 100px;
	}
}

.writer-info {
	&__profile {
		
		padding: 20px 8px;
		margin-bottom: 30px;

		@media (min-width: 768px) {
			background: #FFFFFF;
			box-shadow: -5px -1px 8px 8px rgba(248, 242, 240, 0.28);
		}

		@media (min-width: 992px) {
			margin-bottom: 40px;
		}

		.img {
			width: 90px;
			height: 90px;
			border-radius: 50%;

			img {
				width: 100%;
				height: 100%;
				border-radius: 50%;
				object-fit: cover;
				object-position: center;
			}
		}
	}
}

.trending-blogs {
	&__item {
		a {
			padding: 20px 0 22px 0;
			border-bottom: 1px solid rgba(34, 20, 41, 0.1)
		}

		&:last-child {
			a {
				border-bottom: 0;
				padding-bottom: 0;
			}
		}

		&:first-child {
			a {
				padding-top: 0;
			}
		}

		h3 {
			margin-top: 0 !important;
		}
	}
}

.blog-h2 {
	font-size: 32px;
	line-height: 42px;
	letter-spacing: -1px;

	@media (min-width: 768px) {
		font-size: 35px;
		line-height: 45px;
		letter-spacing: -1px;
	}
	@media (min-width: 992px) {
		font-size: 40px;
		line-height: 50px;
		letter-spacing: -2px;
	}
}

.blog-detail-content {
	&__head {
		margin-bottom: 60px;

		@media (min-width: 992px) {
			margin-bottom: 80px;
		}
	}

	&__item {
		margin-bottom: 60px;

		@media (min-width: 992px) {
			margin-bottom: 80px;
		}

		&:last-child {
			margin-bottom: 0;
		}


		img {
			max-width: 100%;
			height: auto;
			transition: 0.4s ease-in-out;

			&:hover {
				transform: scale(0.98) !important;
			}
		}

		.blog-disc:last-of-type {
			margin-bottom: 0;
		}

		h3 {
			margin-top: 20px;
			margin-bottom: 10px !important;

			@media (min-width: 992px) {
				margin-top: 80px;
			}
		}

		
		h4 {
			margin-top: 20px;
			margin-bottom: 10px !important;

			@media (min-width: 992px) {
				margin-top: 24px;
			}
		}

		h2 {
			margin-top: 45px;
			margin-bottom: 10px !important;

			@media (min-width: 992px) {
				margin-top: 80px;
			}

			&:first-of-type {
				margin-top: 0;
			}
		}

		p+h2 {
			margin-top: 45px !important;
			margin-bottom: 10px !important;

			@media (min-width: 992px) {
				margin-top: 80px !important;
			}

		}

	h2+img, h3+img {
			margin-bottom: 10px !important;
		}

	

		

		.blog-d-img {
			margin-top: 10px;

			@media (min-width: 768px) {
				margin-top: 20px;
			}

			@media (min-width: 992px) {
				margin-top: 30px;
			}
		}

		ol {
			li {
				position: relative;
				counter-increment: step-counter;
				padding-left: 3px;
				list-style-type: auto;
			}
		}

		ul {
			li {
				list-style-type: disc;
			}
		}

		p {
			a {
				position: relative;
				transition: 0.3s ease-in-out;

				color: #fd2e35;

				* {
					color: #fd2e35;
				}

				&::before {
					content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 1px;
				background-color: #fd2e35;
				transform: scaleX(0);
				transform-origin: bottom left;
				transition: 0.3s ease-in-out;
				}

				&:hover {
					&::before {
						transform: scaleX(1);
					}
				}
			}
		}

		
	}

	p,li {
		@media (min-width: 992px) {
			font-size: 18px !important;
			line-height: 32px !important;
			letter-spacing: normal;
		}
	}


}

.blog-disc {
	margin-bottom: 60px;

	@media (min-width: 992px) {
		margin-bottom: 80px;
	}

	&__item {
		margin-bottom: 20px;
		

		@media (min-width: 768px) {
			margin-bottom: 30px;
		}

		@media (min-width: 992px) {
			margin-bottom: 40px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		.step-list {
			&__item {
				margin-bottom: 15px;

				@media (min-width: 992px) {
					margin-bottom: 20px;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			li {
				&::before {
					font-weight: 400;
				}
			}
		}
	}
}

.mb-sub-head {
	margin-bottom: 10px;

	@media (min-width: 768px) {
		margin-bottom: 20px;
	}

	@media (min-width: 992px) {
		margin-bottom: 30px;
	}
}

.blog-d-img {
	margin-bottom: 16px;

	@media (min-width: 768px) {
		margin-bottom: 30px;
	}

	@media (min-width: 992px) {
		margin-bottom: 40px;
	}
}

.conclusion {
	@media (min-width: 768px) {
		padding: 30px 60px;
		background-color: #ffffff;
	}

	@media (min-width: 992px) {
		padding: 40px 90px;
	}
}

.sec-concluison {
	@media (max-width: 767px) {
		background-color: #ffffff;
	}

	padding-top: 30px;
	padding-bottom: 0;

	@media (min-width: 768px) {
		padding-top: 0;
		padding-bottom: 75px;
	}

	@media (min-width: 992px) {
		padding-top: 25px;
		padding-bottom: 115px;
	}
}

.sec-blog {
	.reveal {
		@media (max-width: 767px) {
			width: calc(100% + 40px);
			margin-left: -20px;
		}
	}
}

.blog-reading-progress {
	position: fixed;
	bottom: 20px;
	left: 20px;
	width: 0;
	height: 4px;
	background-color: #fd2e35;
	transition: 0.3s ease-in-out;
	z-index: 99;

	@media (max-width: 767px) {
		left: 0;
		bottom: 0;
	}
}


.auth-nm {
	&__item {
		position: relative;

		&::after {
			position: absolute;
			content: ",";
			right: -5px;
		}

		&:last-child {
			&::after {
				display: none;
			}
		}
	}
}
