.img-fit {
	max-width : 100%;
	object-fit: cover;
}

.img-h-fit {
	width     : 100%;
	min-height: 400px;
	object-fit: cover;

	&--h {
		min-height: 320px;
	}

	@include media-breakpoint-down(xl) {
		min-height: 171px;
		height    : 100%;
		max-height: 171px;
	}
}

.icon-awards {

	@include media-breakpoint-down(lg) {
		width       : 60px;
		margin-right: 12px;
	}
}