.ask-Q {
    @media (min-width: 768px) {
        margin-top: 30px;
    }

    @media (min-width: 992px) {
        margin-top: 40px;
    }
}

.faqs {
    &__item {
        margin-bottom: 35px;

        @media (min-width: 768px) {
            margin-bottom: 40px;
        }

        @media (min-width: 992px) {
            margin-bottom: 50px;
        }

        @media (min-width: 1200px) {
            margin-bottom: 60px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.faq {
    margin-bottom: 16px;
    display: flex;
}

.faq-link {
    position: relative;

    &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 0;
        height: 1px;
        background: #FD2E35;
        transition: 0.4s ease-in-out;
        opacity: 0;
    }

    &:hover {
        color: #FD2E35;
       &::after {
        width: 100%;
        opacity: 1;
       }
    }
}
