.red-stroke {
	position: relative;
	

	@media (min-width: 768px) {
		padding-left: 15px;
	}

	@media (min-width: 992px) {
		padding-left: 18px;
	}


	&::before {
		position: absolute;
		content: "";
		top: 7px;
		left: 0;
		width: 3px;
		height: calc(100% - 14px);
		background: #FD2E35;

		@media (max-width: 767px) {
			display: none !important;
			content: none !important;
		}
	}

	&--mb {
		padding-left: 15px;

		&::before {
			@media (max-width: 767px) {
				display: block !important;
				content: "" !important;
			}
		}
		
	}

	&--small {
		&::before {
			height: 23px;
			left: -15px;

			@media (min-width: 768px) {
				width: 5px;
				top: 12px;
			}

			@media (min-width: 992px) {
				
				top: 15px;
			}
		}
	}

	&--small12 {
		padding: 0;
		&::before {
			height: 20px;
			left: -10px;
			top: 11px;

			@media (min-width: 768px) {
				height: 30px;
				width: 5px;
				top: 12px;
				left: -25px;
			}

			@media (min-width: 992px) {
				height: 40px;
				left: -40px;
				top: 15px;
			}
		}
	}

	&--small13 {
		&::before {
			height: 23px;
			left: 1px;
			top: 11px;
	
			@media (min-width: 768px) {
				height: 34px;
				width: 5px;
				top: 12px;
				left: -25px;
			}
	
			@media (min-width: 992px) {
				height: 53px;
				left: -40px;
				top: 18px;
			}
		}
	}

	&.fnt-50 {
		&::before {
			@media (min-width: 768px) {
				height: 28px;
				left: -20px;
				top: 12px;
			}

			@media (min-width: 992px) {
				height: 45px;
				left: -40px;
				top: 15px;
			}
		}
	}

	&.fnt-60 {
		&::before {
			height: 22px;

			@media (min-width: 768px) {
				height: 28px;
				left: -30px;
				top: 12px;
			}
			@media (min-width: 992px) {
				height: 38px;
				left: -40px;
				top: 16px;
			}
		}
	}

	&.fnt-78 {
		&::before {
			height: 23px;
			left: 1px;
			top: 10px;

			@media (min-width: 768px) {
				height: 28px;
				left: -20px;
				top: 12px;
			}
			@media (min-width: 992px) {
				height: 45px;
				left: -40px;
				top: 15px;
			}
		}
	}
}

.portfolio-img {
	width: 100%;
	height: auto;
	object-fit: cover;

	@media (min-width: 768px) {
		height: 350px !important;
	}

	@media (min-width: 992px) {
		height: 472px !important;
	}
}

.clients-logo {
	&__item {
		margin-top: 25px;

		@media (min-width: 768px) {
			margin-top: 45px;
		}

		@media (min-width: 992px) {
			margin-top: 65px;
		}

		&:nth-child(1) {
			margin-top: 0;
		}

		&:nth-child(2) {
			margin-top: 0;
		}

		&:nth-child(3) {
			margin-top: 0;
		}

		&:nth-child(4) {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}
	}
}

.portfolioimage {
	@media (max-width: 767px) {
		width: calc(100% + 40px);
		margin-left: -20px;
	}
}


// Project Detail css

.project-type {
	span {
		display: inline-block;
		margin-right: 20px;
		position: relative;
		padding-right: 29px;

		&::after {
			position: absolute;
			content: "";
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 9px;
			height: 9px;
			background-color: #C4C4C4;
			border-radius: 50%;
		}

		&:last-of-type {
			padding-right: 0;

			&::after {
				display: none;
				content: none;
			}
		}
	}
}

.disc-head {
	letter-spacing: 1.8px !important;
	@media (min-width: 768px) {
		padding-top: 4px;
		position: sticky;
		top: 50px;
		
	}

	@media (min-width: 992px) {
		padding-top: 5px;
	}
}

.img-project-dtl {
	width: 100%;
	max-width: 100%;
	height: auto;
	object-fit: cover;
}

.project-step {
	&__item {
		margin-bottom: 30px;

		@media (min-width: 768px) {
			margin-bottom: 60px;
		}

		@media (min-width: 992px) {
			margin-bottom: 80px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		// p {
		// 	&:last-of-type {
		// 		margin-bottom: 0;
		// 	}
		// }
	}
}

.project-step-disc {
	&__item {
		margin-bottom: 25px;

		@media (min-width: 768px) {
			margin-bottom: 30px;
		}

		@media (min-width: 992px) {
			margin-bottom: 40px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		p {
			

			@media (min-width: 768px) {
				margin-bottom: 25px;
			}

			@media (min-width: 992px) {
				margin-bottom: 30px;
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}



