
.sec-client {
    .icon-right-arr {
        font-size: 30px;
        cursor: pointer;
        transition: 0.3s ease-in-out;

        &:hover {
            &::before {
                color: #ffffff;
            }
        }
    }
}

.client-img {
    img {
        width: 100%;
        max-width: 100%;
        height: auto;
        object-fit: cover;
        object-position: center;
    }
}

.award-list {
    &__item {
        margin-bottom: 50px;

        @media (min-width: 768px) {
            margin-bottom: 0;
            margin-top: 50px;
        }

        @media (min-width: 992px) {
            margin-top: 70px;
        }

        @media (min-width: 1200px) {
            margin-top: 85px;
        }

        &:nth-child(1) {
            margin-top: 0;  
        }

        &:nth-child(2) {
            margin-top: 0;  
        }

        &:nth-child(3) {
            margin-top: 0;  
        }

        &:last-child {
            margin-bottom: 0;
        }

        .award {
            &__icon {
                margin-bottom: 15px;
            }
        }
    }
}

.sec-head {
    margin-bottom: 45px;

    @media (min-width: 768px) {
        margin-bottom: 50px;
    }

    @media (min-width: 992px) {
        margin-bottom: 60px;
    }
}
