//  SWIPER BUTTON 
.swiper-button-next,
.swiper-button-prev {
    &:after {
        display: none;
    }
}

// LP AWARDS LIST
.lp-awards {
    &__right {

        @include media-breakpoint-down(lg) {
            display       : flex;
            align-items   : center;
            flex-direction: row;
        }
    }
}

.lp-awards-list {
    .img {
        width     : 100%;
        height    : 200px;
        object-fit: cover;

        @media(min-width: 1200px) {
            // min-height: 570px;
            min-height: 343px;
        }

        @media(min-width: 1400px) {
            // min-height: 570px;
            min-height: 500px;
        }
    }

    .desc {
        @include media-breakpoint-up(lg) {
            max-width: 324px;
        }
    }

    &__item {
        padding-right: 0 !important;
        padding-left: 15px;
        width: 250px;
        @media (min-width: 768px) {
            padding-left: 0 !important;
            padding-right: 16px !important;
        }

        &:nth-child(even) {
            .img {
                min-height: 300px;
                @media(min-width: 768px) {
                    // min-height: 570px;
                    min-height: 300px;
                }

                @media(min-width: 992px) {
                    min-height: 566px;
                }
        
               
            }

            @media (min-width: 992px) {
                width: 454px;
            }
        }

        &:nth-child(odd) {
           
            @media (min-width: 992px) {
                width: 654px;
            }

            .img {
               
                @media(min-width: 768px) {
                    // min-height: 570px;
                    min-height: 380px;
                }

                @media(min-width: 992px) {
                    min-height: 450px;
                }
        
            }
        }

       
    }
}

// SWIPER 
.swiper-navigation {
    position: relative;
}

.swiper-navigation-mySwiper {
    position       : absolute;
    right          : 0;
    width          : 100%;
    height         : 123px;
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    background     : red;

    >div {
        position  : absolute;
        width     : 100%;
        text-align: right;
        display   : block;
    }
}