// LP OUR SERVICES - CARD
.lp-card {
	padding: 1.5rem 1.25rem;
	@extend .border-0;
	@extend .me-4;
	@extend .mb-4;

	@include media-breakpoint-up(lg) {
		padding: 2.5rem 2.25rem;
	}
}

// LP OUR SERVICES - TITLE
.lp-card-title {
	@extend .fnt-26;
	@extend .fnt-800;
	@extend .clr-second;
	@extend .my-0;
}

// LP OUR SERVICES - CARD TEXT
.lp-card-text {
	@extend .fnt-20;
	@extend .my-3;
}

// thumbnials

.lp-thumbnial {
	@include media-breakpoint-up(md) {
		margin-bottom: 1rem;
	}

	@include media-breakpoint-up(xl) {
		margin-bottom: 2rem;
	}

	// border-right

	&-borderlft {
		@include media-breakpoint-up(lg) {
			border-right: 1px solid rgba($color: #908a94, $alpha: 0.25);
		}
	}

	&:last-child {
		.lp-thumbnial-borderlft {
			border-right: none;
		}
	}

	&--sm {
		@include media-breakpoint-up(lg) {
			max-width: 260px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 280px;
		}
	}

	&__info {
		@include media-breakpoint-up(lg) {
			display        : flex;
			flex-direction : column;
			align-items    : center;
			justify-content: space-between;
			width          : 100%;
		}

		@include media-breakpoint-up(xl) {
			padding : {
				left : 2rem;
				right: 2rem;
			}
		}
	}

	&__body {
		padding-top: 2rem;
	}

	&__heading {
		@extend .fnt-18;
	}

	&__text {
		@extend .fnt-14;
	}
}